
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
html {
  height: 100%;
}
body {
  background: linear-gradient(135deg, #68c9b6, #3677ce) no-repeat;
  background-attachment: fixed;
  height: 100%;
}



